<template>
  <div id="browsingHistory">
    <div class="header">
      <div class="van-hairline--bottom van-nav-bar">
        <div class="van-nav-bar__left">
          <i class="van-icon van-icon-arrow van-nav-bar__arrow" style="color: #000000 !important;" @click="$router.back(-1)"></i>
        </div>
        <div class="van-ellipsis van-nav-bar__title">浏览历史</div>
      </div>
    </div>
    <div class="login_box">
      <van-list v-model="loading" :finished="finished" @load="onload" :offset="100">
        <div style="margin-top: 5px" v-for="(item, index) in followList" :key="index" v-if="item.goods">
          <div class="text_center font_12 color_8b8b8b" style="padding: 10px;">
            <span>{{item.addtime}}</span>
          </div>
          <van-card style="margin: 4px 10px;border-radius: 3px" @click.native="toGoods(item)">
            <div slot="thumb">
              <img :src="item.goods.goodslogo.split(',')[0]" v-lazy="item.goods.goodslogo.split(',')[0]" width="100%" style="display: block;" />
            </div>
            <div slot="tags" class="font_weight_bold padding_bottom_5">
              {{item.goods.goodsname}}
            </div>
            <div slot="tags" class="font_12 color_8b8b8b padding_bottom_5" style="height: 36px;overflow: auto">
              {{item.goods.introduce}}
            </div>
            <!--<div slot="tags" class="padding_bottom_5 font_12">-->
              <!--&nbsp;-->
            <!--</div>-->
            <div slot="tags" class="font_12 color_8b8b8b padding-top-5">
              <span v-if="item.goods.type==1">¥{{item.goods.price}}</span>
              <span v-if="item.goods.type==2">¥{{item.goods.spike}}</span>
              <span class="padding-left-20 float_right">{{item.goods.paynum}}人付款</span>
            </div>
          </van-card>
        </div>
      </van-list>
    </div>
  </div>
</template>
<script>
  export default {
    name: 'browsingHistory',
    data() {
      return {
        loading: false,
        finished: false,
        pageNum: 1,
        followList:[],
        userId:localStorage.getItem('userId'),
      }
    },
    methods:{
      onload(){
        this.loading = true;
        this.getList();
      },
      toGoods(dataMap){
        if(dataMap.type==1){
          this.$store.commit('setGoodsInfo', dataMap.goods);
          this.$router.push('goods');
        }else{
          this.$store.commit('setGoodsInfo', dataMap.goods);
          this.$router.push('goodsKill');
        }
      },
      getList(){
        setTimeout(() => {
          let $this = this;
          let obj = {
            userid: $this.userId,
            pageNum: $this.pageNum
          };
          let qs = require('qs');
          $this.$ajax.post("watch/getwatchlistgoods", qs.stringify(obj), {
            headers: {
              'Content-Type': 'application/x-www-form-urlencoded'
            }
          })
          .then(function(rpdata) {
            if(rpdata.data.meta.code=='200'){
              if(rpdata.data.data.data.length>0){
                $this.followList = $this.followList.concat(rpdata.data.data.data);
                $this.pageNum++;
                $this.loading = false;
              }else{
                $this.finished = true;
                $this.loading = false;
              }
            }
          })
          .catch(function(err) {})
        }, 1000);
      },
    }
  }
</script>
<style>
  #browsingHistory .van-card__thumb {
    top: 0px;
    left: 5px;
    width: 90px;
    height: 90px;
    position: absolute;
    overflow: hidden;
  }
  #browsingHistory .van-card {
    height: 90px;
    padding: 5px 8px 5px 100px;
  }
  #browsingHistory .login_box{
    margin-top: 50px;
  }
</style>



